// Width
@for $i from 0 through 30 {
  .w-#{$i} {
    width: 10px * $i !important;
  }
}

.w-auto {
  min-width: auto;
  width: auto;
}

// Height
.h-minus-nav {
  height: calc(100vh - 60px) !important;
}
