@mixin loader-default {
  .loader {
    width: $loader-size-default;
    height: $loader-size-default;
  }

  .button.has-loader {
    padding: 3px 7px;

    &.has-icon {
      padding: 3px 7px;
    }

    &.is-solid, &.is-danger {
      .loader:before {
        content: url(data:image/svg+xml;base64,);
      }
    }

    .loader {
      width: 30px;
      height: 30px;
      margin: 0px auto;
    }
  }

  .loader:before {
    content: url(data:image/svg+xml;base64,);
  }

  .loader.is-inverse:before {
    content: url(data:image/svg+xml;base64,);
  }

  .loader.is-extrasmall {
    width: $loader-size-xs;
    height: $loader-size-xs;
  }

  .loader.is-small {
    width: $loader-size-sm;
    height: $loader-size-sm;
  }

  .loader.is-medium {
    width: $loader-size-md;
    height: $loader-size-md;
  }

  .loader.is-large {
    width: $loader-size-lg;
    height: $loader-size-lg;
  }
}

@mixin loader-theme {
    .loader {
      width: $loader-size-default;
      height: $loader-size-default;
    }

    .loader.is-extrasmall {
      width: $loader-size-xs;
      height: $loader-size-xs;
    }

    .loader.is-small {
      width: $loader-size-sm;
      height: $loader-size-sm;
    }

    .loader.is-medium {
      width: $loader-size-md;
      height: $loader-size-md;
    }

    .loader.is-large {
      width: $loader-size-lg;
      height: $loader-size-lg;
    }
}